<template>
  <section class="hero" id="hero" data-scroll-index="0" v-bind:style="{ backgroundImage: 'url(' + data.image + ')' }">
    <div class="container">
      <div class="caption">
        <h1>{{ data.name }}</h1>
        <p>{{ data.specialized }}</p>
      </div>
      <div class="arrow">
        <button class="icon-arrow-down" data-scroll="#about_me"></button>
      </div>
    </div>
  </section>
</template>

<script>
  import {
    db
  } from '@/firebase'

  export default {
    name: 'Hero',
    data() {
      return {
        data: '',
      }
    },
    firebase: {
      data: db.ref('hero'),
    },
    created: function () {
      // `this` trỏ đến đối tượng Vue hiện hành
      // console.log(this.data)
    }
  }
</script>

<style scoped>
  .hero {
    position: relative;
    height: 100vh;
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
    color: var(--white);
  }

  .hero::before {
    content: " ";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #000;
    opacity: .5;
  }

  .caption {
    position: absolute;
    top: 50%;
    transform: translate3d(0, -50%, 0);
  }

  .caption h1 {
    font-family: var(--font-family-monospace);
    font-size: 2.6rem;
  }

  .caption p {
    font-size: 1rem;
    opacity: .9;
  }

  .hero .arrow {
    position: absolute;
    bottom: 10vh;
    width: 100vw;
    padding-left: 50px;
    cursor: pointer;
    animation: bounce 2s infinite;
  }

  @keyframes bounce {

    0%,
    20%,
    50%,
    80%,
    100% {
      transform: translateY(0);
    }

    40% {
      transform: translateY(-30px);
    }

    60% {
      transform: translateY(-15px);
    }
  }

  .hero .arrow .icon-arrow-down {
    border: 0;
    background: transparent;
    font-size: 3rem;
    padding: 0;
    color: var(--white);
  }

  @media (min-width: 768px) {
    .caption h1 {
      font-size: 4.5rem;
    }

    .caption p {
      font-size: 1.2rem;
    }

    .hero .arrow .icon-arrow-down {
      font-size: 4rem;
    }
  }
</style>