<template>
  <div class="home">
    <Hero />
    <AboutMe />
    <ProjectsMe />
    <Contact />
    <Footer />
  </div>
</template>

<script>
  // @ is an alias to /src
  
  import Hero from '@/components/Hero.vue'
  import AboutMe from '@/components/AboutMe.vue'
  import ProjectsMe from '@/components/ProjectsMe.vue'
  import Contact from '@/components/Contact.vue'
  import Footer from '@/components/Footer.vue'

  export default {
    name: 'home',
    components: {
      Hero,
      AboutMe,
      ProjectsMe,
      Contact,
      Footer
    }
  }

  window.onload = function () {

    class Tweezer {
      constructor(opts = {}) {
        this.duration = opts.duration || 1000;
        this.ease = opts.easing || this._defaultEase;
        this.start = opts.start;
        this.end = opts.end;

        this.frame = null;
        this.next = null;
        this.isRunning = false;
        this.events = {};
        this.direction = this.start < this.end ? 'up' : 'down';
      }

      begin() {
        if (!this.isRunning && this.next !== this.end) {
          this.frame = window.requestAnimationFrame(this._tick.bind(this));
        }
        return this;
      }

      stop() {
        window.cancelAnimationFrame(this.frame);
        this.isRunning = false;
        this.frame = null;
        this.timeStart = null;
        this.next = null;
        return this;
      }

      on(name, handler) {
        this.events[name] = this.events[name] || [];
        this.events[name].push(handler);
        return this;
      }

      emit(name, val) {
        let e = this.events[name];
        e && e.forEach(handler => handler.call(this, val));
      }

      _tick(currentTime) {
        this.isRunning = true;

        let lastTick = this.next || this.start;

        if (!this.timeStart) this.timeStart = currentTime;
        this.timeElapsed = currentTime - this.timeStart;
        this.next = Math.round(this.ease(this.timeElapsed, this.start, this.end - this.start, this.duration));

        if (this._shouldTick(lastTick)) {
          this.emit('tick', this.next);
          this.frame = window.requestAnimationFrame(this._tick.bind(this));
        } else {
          this.emit('tick', this.end);
          this.emit('done', null);
        }
      }

      _shouldTick(lastTick) {
        return {
          up: this.next < this.end && lastTick <= this.next,
          down: this.next > this.end && lastTick >= this.next
        } [this.direction];
      }

      _defaultEase(t, b, c, d) {
        if ((t /= d / 2) < 1) return c / 2 * t * t + b;
        return -c / 2 * ((--t) * (t - 2) - 1) + b;
      }
    }

    const elements = document.querySelectorAll('[data-scroll]');

    if (elements) {
      for (let i = 0; i < elements.length; i++) {

        const button = elements[i];
        const targetValues = elements[i].getAttribute('data-scroll');
        const targets = document.querySelectorAll(targetValues);
        if (targets) {
          for (let i = 0; i < targets.length; i++) {
            button.addEventListener('click', () => {
              const scrollTop = (window.scrollY || window.pageYOffset || document.documentElement.scrollTop);
              new Tweezer({
                  start: scrollTop,
                  end: targets[i].getBoundingClientRect().top + scrollTop,
                })
                .on('tick', v => window.scrollTo(0, v))
                .begin();
            });
          }
        } else {
          console.log('wrong id');
        }
      }
    }

  }
</script>